@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
*{
  box-sizing: border-box;
}

html{
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
  line-height: 26px;
  padding-top: 100px;
  margin: 0;
  font-family: 'Rubik', sans-serif;
}

.bg-img{
  position: absolute;
  top: 500px;
  z-index: -100000;
}

.top-header{
  position: absolute;
  top: 0;
  width: 100%;
  background-color: white;
  z-index: 10;
}

.header-container{
  height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 1150px;
  margin: auto;
  padding: 0 10px;
}

@keyframes header{
  0%{translate: 0 -100px;}
  100%{translate: 0 0px;}
}

.logo-container{
  width: 140px;
  margin-right: 16px;
  padding: 5px 0 5px 0;
  object-fit: scale-down;
}

.navbar-container{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.navBar-elements{
  font: 500 14px/100px "Rubik", sans-serif;
  color: #000000;
  text-decoration: none;
  margin-right:45px ;
  text-transform: uppercase;
}

.selected-nav{
  color: #4458dc;
}

.primary_btn {
  display: inline-block;
  color: #fff;
  letter-spacing: 0px;
  font-family: "Rubik", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 46px;
  outline: none !important;
  text-align: center;
  cursor: pointer;
  text-transform: uppercase;
  border-radius: 5px;
  background-origin: border-box;
  background-clip: content-box, border-box;
  background-image: linear-gradient(to right, #4458dc 0%, #854fee 100%), radial-gradient(circle at top left, #4458dc, #854fee);
  border: double 2px transparent;
  box-shadow: 0px 10px 30px rgba(118, 85, 225, 0.3);
}

main{
  width: 1150px;
  height: 900px;
  padding: 0 10px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 100px;
  position: relative;
}

.banner-container > *{
  text-transform: uppercase;
}

.banner-container{
  padding-top: 8rem;
  width: 635px;
}

.main-title{
  margin: 0;
  font-weight: 500;
  font-size: 40px;
  margin-bottom: 20px;
  position: relative;
}

.hello-line{
  position: absolute;
  top: 46%;
  right: 40px;
  width: 450px;
}

.main-name{
  text-transform: capitalize;
  font-weight: 500;
  margin-top: 20px;
  font-size: 63px;
  line-height: 60px;
  margin-bottom: 25px;
}

.main-info{
  margin: 0;
  font-weight: 500;
  font-size: 24px;
  margin-bottom: 35px ;
}

.main-button{
  padding: 0 42px;
}

.main-image-wraper{
  position: relative;
  width: 100px;
}

.main-image{
  position: absolute;
  top: 0px;
  right: -270px;
}

.purpel-dot{
  z-index: 1;
  width: 24px;
  position: absolute;
  left: 38%;
  top: 0px;
}

.green-dot{
  z-index: 1;
  height: 55px;
  position: absolute;
  left: 34%;
  top: 10px;
}

.heart1{
  z-index: 1;
  height: 40px;
  position: absolute;
  left: -15%;
  top: 10px;
}

.heart2{
  z-index: 1;
  height: 40px;
  position: absolute;
  right:-27%;
  top: 170px;
}

.lamp-image1{
  z-index: -1;
  height: 70px;
  position: absolute;
  right:-4.3%;
  top: 77px;
}

.lamp-image2{
  z-index: 1;
  height: 70px;
  position: absolute;
  right: 58%;
  top: 620px;
}


.name-image{
  z-index: -1;
  height: 300px;
  position: absolute;
  left:-36%;
  top: 155px;
  translate: 0px 0px;
}

.about-section{
  padding-bottom: 70px;
  padding-top: 100px;
}

.about-conatainer{
  position: relative;
  padding-bottom: 150px;
  display: flex;
  width: 1150px;
  justify-content: start;
  align-items: center;
  margin: auto;
  margin-top: 100px;
}

.about-text-container{
  height: 631px;
}

.about-img-container{
  position: relative;
  flex: 0 0 41.666667%;
  max-width: 41.666667%;
}

.about-image{
  position: absolute;
  top: -350px;
  left: -220px;
}

.about-text-container{
  margin-top: 100px;
  margin-left: 8.333333%;
}

.about-tilte{
  font-size: 36px;
  font-weight: 700;
  line-height: 50px;
  margin-bottom:15px ;
  text-transform: uppercase;
}

.about-text{
  color: #777777;
  margin-bottom: 20px;
  font-family: 'Roboto', sans-serif;
  font-size: 15px;
  font-weight: 400;
}

.about-btn{
  margin-top: 20px;
  padding: 0 42px;
}

.experience-conatainer{
  padding: 25px 30px;
  background: #faf8ff;
  text-align: center;
  position: relative;
  margin-top: 25px;
  width: 350px;
  margin: auto;
}

.experience-conatainer::before{
  content: '';
  position: absolute;
  top: -25px;
  left: -50px;
  background: url(https://preview.colorlib.com/theme/satner/img/client-info-bg.png) no-repeat;
  height: 175px;
  width: 175px;
}

.years-experience-conatainer{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}

.year-experience{
  color: #854fee;
  font-family: "Rubik", sans-serif;
  font-size: 100px;
  font-weight: 700;
  line-height: 85px;
  position: relative;
  z-index: 9;
}

.experience-text{
  text-align: left;
  color: #000000;
  font-family: "Rubik", sans-serif;
  font-size: 20px;
  margin-left: 20px;
  font-weight: 500;
}

.call-conatainer{
  padding-top: 50px;
  text-align: left;
  margin-bottom: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.phone-icon{
  height: 60px;
}

.phone-number-container{
  margin-left: 15px;
}

.phone-number-container p{
  margin:0px ;
  text-align: start;
  color: #854fee;
  font-size: 16px;
  text-transform: uppercase;
  margin-bottom: 5px;
}

.phone-number-container h3{
  margin:0px ;
  margin-bottom:8px ;
}

.Services-section{
  padding-top: 100px;
  padding-bottom: 35px;
}

.Services-header{
  width: 1150px;
  text-align: center;
  margin: auto;
  margin-bottom:100px ;
}

.Services-header-tile{
  font-size: 36px;
  font-weight: 700;
  margin-bottom: 15px;
  line-height: 50px;
  text-transform: uppercase;
}

.Services-header-text{
  color:#777777;
  font-size: 15px;
}

.cards-container{
  display: grid;
  grid-template-columns: 285px 285px 285px 285px;
  max-width: 1150px;
  margin: auto;
  gap: 30px;
}

.service-card{
  cursor: pointer;
  padding: 45px 10px;
  text-align: center;
  background: #f9f9fd;
  cursor: pointer;
  margin-bottom: 30px;
  -webkit-transition: all 0.4s ease 0s;
  -moz-transition: all 0.4s ease 0s;
  -o-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
}

.service-card:hover{
  box-shadow: 0px 15px 30px rgba(77, 87, 222, 0.3);
  background: #fff;
}

.service-img{
  height: 80px;
  margin-bottom: 35px;
}

.service-title{
  font-size: 20px;
  margin: 0;
  margin-top: 35px;
  margin-bottom: 20px;
  font-weight: 500;
  text-transform: uppercase;
}

.service-text{
  margin: 0;
  font-size: 15px;
  color:#777777;
}

.Portfolio-section{
  padding-top: 100px;
}

.Portfolio-title{
  padding: 0 5px;
  max-width: 1140px;
  margin: auto;
  font-size: 36px;
  font-weight: 700;
  margin-bottom: 15px;
  line-height: 50px;
  text-transform: uppercase;
  height: 200px;
}

.projects-container{
  max-width: 1140px;
  padding: 0 5px;
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.project-card-container{
  padding: 0 15px;
  margin-bottom:80px ;
  text-decoration: none;
  overflow: hidden;
}

.project-card-img-container{
  position: relative;
}

.project-card-img{
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  width: 100%;
  height: auto;
  border-radius: 5px;
}

.project-card-title{
  font-size: 20px;
  color: #000000;
  font-weight: 500;
  margin-bottom: 0;
}

.project-card-text{
  font-size: 15px;
  color: #777777;
  margin: 0;
}

.overlay{
  border-radius: 5px;
  position: absolute;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 98%;
  background: rgba(77, 87, 222, 0.5);
  opacity: 0;
  text-align: center;
  transition: all 0.4s ease 0s;
}

.project-card-img-container:hover .overlay{
  opacity: 1;
}

.project-card-img-container:hover .overlay-img{
  top: 30%;
  left: 35%;
  opacity: 1;
}

.overlay-img{
  position: absolute;
  opacity: 0;
  top: 10%;
  left: 10%;
  height: 100px;
  transition: all 0.4s ease 0s;
}

.front-end-img{
  height: 75px;
}

.scripts-img{
  height: 67px;
  margin-bottom: 5px;
}

.contact-section{
  width: 100%;
  padding-top: 100px;
}

.contact-title{
  margin: auto;
  text-align: center;
  text-transform: uppercase;
  font-size: 50px;
  font-weight: 700;
  margin-bottom: 15px;
  line-height: 50px;
  text-transform: uppercase;
}

.contact-title-text{
  margin: auto;
  text-align: center;
  text-transform: uppercase;
  font-weight: 500;
  margin-bottom: 100px;
}

.contact-body-wrpper{
  margin: auto;
  max-width: 1140px;
  padding: 0 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.contact-email-tilte{
  text-align: left;
  margin-bottom: 32px;
}

.email-text{
  font-size: 2.7rem;
  line-height: 3.125rem;
  font-weight: bold;
  color: black;
  text-decoration: none;
}

.email-text:hover{
  color: #525252;
}

.contact-socials-tilte{
  text-align: left;
  margin-bottom: 25px;
}

.contact-socials-icons-wrpper{
  display: flex;
  justify-content: space-between;
}

.contact-socials-circle{
  width: 58px;
  height: 58px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  z-index: 2;
  background-color: #4d56de;
}

.contact-socials-circle::before{
  content: "";
  width: 58px;
  height: 58px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  border-radius: 50%;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: white;
  opacity: 0.1;
}
  
  
.contact-socials-circle img{
z-index: 2;
transition: all 200ms ease-in-out;
}

.contact-socials-circle:hover img {
  transform: scale(1.2);
}

@media(max-width: 1670px){
  .main-image-wraper,.about-img-container{
    width: 490px;
  }
  .main-image{
    top: 0;
    left: 15%;
    max-width: 100%;
    height: auto;
  }
  .about-image{
    width: 100%;
    top: -250px;
    left: 0px;
  }
}

@media(max-width: 1230px){
  .main-image-wraper,.about-img-container{
    width: 490px;
  }
  .main-image{
    top: 0;
    left: 15%;
    max-width: 100%;
    height: auto;
  }
  .about-image{
    width: 100%;
    top: -250px;
    left: 0px;
  }
}